<script>
	import uniPopup from '@/components/uni-popup/uni-popup.vue';
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue';
	import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue';
	import {
		API
	} from '@/util/api.js';
	export default {
		data() {
			return {
				list: [],
				number: '',
				navIndex: 0,
				type:1,
				titketype:[],
				listData:[],
				transferList:[],
				id:'',
				content:''
			}
		},
		onLoad(option) {
			console.log(option.id)
			this.number = option.id
			this.getData();
			this.trans(this.type)
		},
		onShow() {
			this.request(API.getXieyi, 'post', {
				id: 2
			}).then(res => {
				this.content=res.data.content
				console.log(res)
				
			})
		},
		methods: {
			openpop(id){
				this.id=id
				this.$refs.popup.open()
			},
			goticket(){
				uni.navigateTo({
					url: '/pages/ticket/ticketexchange?' + 'id=' + this.id+'&&adapte_active=0'
				});
			},
			hello(){
				uni.showToast({ title: '只能赠送一次',duration: 2000})
			},
			trans(type) {
               this.request(API.getUserTickRollList, 'GET', {
               	type:type
               }).then(res => {
               	console.log(res)
               	if (res.code === 10000) {
               		this.titketype=res.data
					this.listData=res.data.listData
					this.transferList=res.data.listData
					console.log(this.transferList)
               	}
               });
			},
			getData() {
				//获取明星列表
				this.request(API.getList, 'GET', {
					limit: 10,
					page: 1
				}).then(res => {
					console.log(res)
					if (res.code === 10000) {
						this.list = res.data;
					}
				});
			},
			goUrl(id) {
				uni.navigateTo({
					url: '/pages/ticket/exchange?' + 'id=' + id+'&&adapte_active=0'
				});
				console.log(id)
			}
		}

	}
</script>

